@mixin breakpoint($size) {
  @if $size == smob {
    @media (max-width: 374.9px) { @content; }
  }
  @else if $size == mob {
    @media (max-width: 499.9px) and (min-width: 375px) { @content; }
  }
  @else if $size == stab {
    @media (max-width: 956.9px) and (min-width: 500px) { @content; }
  }
  @else if $size == tab {
    @media (max-width: 1365.9px) and (min-width: 957px) { @content; }
  }
  @else if $size == desk {
    @media (min-width: 1366px) { @content; }
  }
  @else if $size == alltab {
    @media (max-width: 1365.9px) and (min-width: 500px) { @content; }
  }
  @else if $size == allmob {
    @media (max-width: 499.9px) { @content; }
  }
  @else if $size == avgmob {
    @media (max-width: 719.9px) { @content; }
  }
  @else if $size == goodtab {
    @media (max-width: 1365.9px) and (min-width: 720px) { @content; }
  }
  @else {
    @warn "Error in breakpoint mixin: supported values: smob, mob, stab, tab, desk, alltab, allmob, avgmob, goodtab";
  }
}

@mixin safeanimate() {
  @media (prefers-reduced-motion: no-preference) { @content; }
}
