@import '/src/modules/styles/common';

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit,
    minmax($gallery-cell-min-width, 50%) minmax($gallery-cell-min-width, 50%));
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  border-bottom: 1px solid black;

  @include breakpoint(desk) {
    grid-template-columns: repeat(auto-fit,
      minmax($gallery-cell-min-width, 33.33%) minmax($gallery-cell-min-width, 33.33%) minmax($gallery-cell-min-width, 33.33%));
  }

  @include breakpoint(avgmob) {
    grid-template-columns: 1fr;
  }
}

.cell {
  width: 100%;
  height: 100%;
  min-width: $gallery-cell-min-width;
  min-height: $gallery-cell-min-height;
  position: relative;
  box-sizing: border-box;
  border-bottom: 1px solid black;

  @include breakpoint(goodtab) {
    &:nth-child(odd) {
      border-right: 1px solid black;
    }
  }

  @include breakpoint(desk) {
    &:nth-child(3n+1),
    &:nth-child(3n+2) {
      border-right: 1px solid black;
    }
  }
}

.cellLast {
  border-bottom: none;
}

.cell img {
  width: 100%;
  height: 100%;
}

.thumbnail {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 0;
}

.thumbnailAspect {
  width: 100%;
  padding-bottom: 73%;
  background: #f4f4f6;
}

.thumbnailTitle {
  position: absolute;
  top: 16px;
  width: 100%;
  text-align: center;
  color: black;
  font-family: $textf;
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
}

@-webkit-keyframes loadingAnimation {
  0% {
    opacity: 1
  }
  50% {
    opacity: .1
  }
  100% {
    opacity: 1
  }
}

@keyframes loadingAnimation {
  0% {
    opacity: 1
  }
  50% {
    opacity: .1
  }
  100% {
    opacity: 1
  }
}

.loading {
  font-family: $textf;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  display: none;

  @include safeanimate {
    -webkit-animation: loadingAnimation 1s infinite;
    animation: loadingAnimation 1s infinite;
  }
}

.loadingShow {
  display: inline-block;
}