@import '/src/modules/styles/common';

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
}


.header {
  position: relative;
  width: 100%;
  height: $collections-head-height;
  box-sizing: border-box;
  border-bottom: 1px solid black;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
  justify-content: space-between;

  @include breakpoint(avgmob) {
    height: $collections-head-height * 2;
  }

  @include breakpoint(desk) {
    grid-template-columns:
      minmax($gallery-cell-min-width, 1fr) minmax($gallery-cell-min-width, 1fr) minmax($gallery-cell-min-width, 1fr);
  }
}

.headerEmpty, .headerTitle {
  box-sizing: border-box;
  height: $collections-head-height;
}

.headerEmpty {
  display: none;
}

@include breakpoint(desk) {
  .headerEmpty, .headerTitle {
    border-right: 1px solid black;
  }

  .headerEmpty {
    display: inline-block;
  }
}

.headerTitle {
  font-family: $textf;
  font-weight: 700;
  font-size: 20px;
  line-height: $collections-head-height;
  text-align: center;
  text-transform: uppercase;
  color: black;

  @include breakpoint(avgmob) {
    border-bottom: 1px solid black;
    height: $collections-head-height;
  }

  @include breakpoint(desk) {
    font-size: 25px;
  }
}

.gallery {
  flex: 1;
  height: 100%;
  width: 100%;
}