@import '/src/modules/styles/common';

.container {
  width: 100%;
  height: $menubar-height;
  position: fixed;
  z-index: $zindex-menu-base;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-bottom: 1px solid black;
  box-sizing: border-box;
}

.spacer {
  height: $menubar-height;
}

.topButton {
  cursor: pointer;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: $menu-btn-width;
  color: black;
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  line-height: 14px;
  text-transform: uppercase;
  height: $menubar-height - 1;
  background: white;
}

.placeholder {
  width: $menu-btn-width;
}

.topButtonSpan {
  height: 15px;
  padding-top: 3px;
  border-bottom: 2px solid white;

  &:hover, &Hover {
    border-bottom-color: black;
  }
}

.home {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.sideContainer {
  position: fixed;
  z-index: $zindex-menu-base + 12;
  top: $menubar-height;
  width: $side-menu-width;
  max-width: $side-menu-width;
  height: calc(100vh - $menubar-height);
  overflow: visible;
  background-color: white;

  @include safeanimate {
    transition: all $menu-slide-duration $menu-slide-func;
  }

  @include breakpoint(avgmob) {
    width: 100%;
    max-width: 100%;
  }
}

@include breakpoint(avgmob) {
  html {
    /* to make sliding menus correctly size for mobile (full-width on screen),
     se must set up permanent scroll bar for correct sizing */
    overflow-y: scroll;
  }
}

.sideOpen {
  left: 0;
}

.sideClose {
  left: -$side-menu-width;
  visibility: hidden;

  @include breakpoint(avgmob) {
    left: -100%;
  }
}

.side {
  display: flex;
  position: relative;
  z-index: $zindex-menu-base + 11;
  flex-direction: column;
  width: 100%;
  height: 100%;
  text-align: left;
  border-right: 1px solid black;
  box-sizing: border-box;
  background: white;

  @include breakpoint(avgmob) {
    box-sizing: content-box;
  }
}

.sideItem {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: $menubar-height;
  border-bottom: 1px solid black;
  cursor: pointer;
  padding-right: 14px;
  box-sizing: border-box;

  &:hover, &Hover {
    background-color: black;
    color: white;
  }

  &Hover:focus {
    outline: 4px solid lightblue;
  }
}

.sideItemTitle {
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  margin-left: 14px;
}

.subContainer {
  position: absolute;
  top: $menubar-height;
  z-index: $zindex-menu-base + 10;
  width: $side-menu-width;
  max-width: $side-menu-width;
  height: calc(100vh - $menubar-height);
  background: white;
  color: black;
  border-right: 1px solid black;
  border-top: 1px solid black;
  box-sizing: border-box;

  @include safeanimate {
    transition: all $menu-slide-duration $menu-slide-func;
  }

  @include breakpoint(avgmob) {
    width: 100%;
    max-width: 100%;
    z-index: $zindex-menu-base + 12;
    overflow: visible;
    box-sizing: content-box;
  }
}

.subOpen {
  left: $side-menu-width;

  @include breakpoint(avgmob) {
    left: 0;
  }
}

.subClose {
  left: 0;
  visibility: hidden;

  @include breakpoint(avgmob) {
    left: -100%;
  }
}

.subItem {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: $menubar-height;
  border-bottom: 1px solid black;
  cursor: pointer;
  box-sizing: border-box;
  padding-left: 14px;

  &:hover {
    background: black;
    color: white;
  }
}

.subItemTitle {
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  display: flex;
  align-items: center;
  text-transform: uppercase;
}
