@import '/src/modules/styles/common';

.buttons {
  position: absolute;
  top: 0;
  right: $filter-button-width;
  display: flex;
  align-items: center;
  justify-content: left;
  box-sizing: border-box;
  border-left: 1px solid black;

  @media (max-width: 956.9px) {
    right: 0;
  }

  @include breakpoint(avgmob) {
    top: $collections-head-height;
    right: unset;
    left: 0;
    border-left: none;
    width: 100%;
    flex-wrap: wrap;
  }

  @include breakpoint(desk) {
    position: relative;
    right: unset;
    border-left: none;
  }
}

.button {
  width: $filter-button-width;
  text-align: center;
  color: black;
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  line-height: $collections-head-height - 1;
  text-transform: uppercase;
  height: $collections-head-height - 1;
  border-right: 1px solid black;
  box-sizing: border-box;
  cursor: pointer;

  @media (max-width: 956.9px) and (min-width: 720px) {
    &:nth-child(2) {
      border-right: none;
    }
  }
}


.buttonActive {
  background: black;
  color: white;
}

.popup {
  position: absolute;
  top: $collections-head-height - 1;
  left: -$filter-popup-width + $filter-button-width;
  z-index: $zindex-menu-base - 1;
  width: $filter-popup-width;
  border: 1px solid black;
  background: white;
  color: black;
  box-sizing: border-box;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;

  @include safeanimate {
    transition: all $menu-slide-duration $menu-slide-func;
  }

  @include breakpoint(avgmob) {
    left: 0;
    width: 100%;
    border-left: none;
    border-right: none;
    position: relative;
  }
}

.popupOpen {
  height: $filter-popup-height;
  visibility: visible;
}

.popupFrames {
  left: -$filter-popup-width + ($filter-button-width * 2);
  padding: 0 8px;

  @include breakpoint(avgmob) {
    left: 0;
  }
}

.item {
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  line-height: 24px;
  text-transform: capitalize;
  height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.colourSample {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  border-radius: 100%;
  padding-top: 2.5px;
  padding-left: 2.5px;
  box-sizing: border-box;
  border: 2px solid white;
}

.sampleImg {
  width: 16px;
  height: 16px;
  border-radius: 100%;
}

.itemSelected {
  color: blue;
}

.itemSelected .colourSample {
  border: 2px solid black;
}

.current {
  width: 100%;
  min-height: $collections-head-height;
  box-sizing: border-box;
  border-bottom: 1px solid black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  padding: 16px;

  @include breakpoint(avgmob) {
    grid-template-columns: 1fr;
    padding: 0;
  }

  @include breakpoint(desk) {
    grid-template-columns:
      minmax($gallery-cell-min-width, 1fr) minmax($gallery-cell-min-width, 1fr) minmax($gallery-cell-min-width, 1fr);
  }
}

.box {
  display: flex;
  flex-wrap: wrap;

  @include breakpoint(avgmob) {
    border-bottom: 1px solid black;
    min-height: $collections-head-height;
  }
}

.currentItem {
  font-family: $textf;
  font-weight: $weightMenu;
  font-size: $sizeMenu;
  line-height: 24px;
  text-transform: uppercase;
  height: 24px;
  padding: 0 12px;
  display: flex;
  align-items: center;
}

.removeButton {
  cursor: pointer;
  margin-left: 8px;
}

.total {
  font-family: $textf;
  font-weight: 700;
  font-size: $sizeMenu;
  text-transform: uppercase;
  text-align: center;

  @include breakpoint(avgmob) {
    height: $collections-head-height;
    border-bottom: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.clear {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  @include breakpoint(avgmob) {
    justify-content: flex-start;
    height: $collections-head-height;
    padding-left: 12px;
  }
}

.clearLink {
  font-family: $textf;
  font-weight: 700;
  font-size: $sizeMenu;
  text-transform: uppercase;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}
